.container {
  margin-top: 0;
  user-select: none; /* Prevents text selection */
  -webkit-user-select: none; /* Safari */
  -moz-user-select: none; /* Firefox */
  -ms-user-select: none; /* IE10+ */
}

.infoContainer {
  flex-wrap: wrap;
  flex-direction: row;
}

.bgimg {
  /* background-image: url("https://images.pexels.com/photos/2670898/pexels-photo-2670898.jpeg?auto=compress&cs=tinysrgb&w=1260&h=750&dpr=2"); */
  height: min-content;
  width: auto;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  overflow-x: hidden;
  /* backdrop-filter: blur(1px); */
   background-color: rgba(0, 0, 0, 1); 
}

.developerimage {
  background: rgba(255, 255, 255, 0.05);
  -webkit-backdrop-filter: blur(0.1px);
  border-radius: 10px;
}

.introtext {
  color: aliceblue;
  font-size: 40px;
  font-family: 'Audiowide';
}

.basedin {
  color: #ffffff;
}

.introsmalltext {
  color: white;
  font-family: 'Audiowide';
}

.animated3dbox {
  background-color: aqua;

}
.skillsdecpt{
  color: white;
  font-family: 'Audiowide';
  margin-bottom: 1em;
  padding: 1em;
}
.loader {
  position: relative;
  overflow: hidden;
  border-right: 3px solid;
  width: 0px;
  animation: typewriter 2s steps(7) alternate , blink 0.5s steps(7);
}
.loader1 {
  position: relative;
  overflow: hidden;
  border-right: 3px solid;
  width: 0px;
  animation: typewriter 4s steps(21)   alternate , blink 0.5s steps(21) ;
}
.loader2 {
  position: relative;
  overflow: hidden;
  border-right: 3px solid;
  width: 0px;
  animation: typewriter 6s steps(15)   alternate , blink 0.5s steps(15) ;
}

.loader-text {
  font-size: 40px;
  font-weight: 700;
  background: linear-gradient(to right,#159957, #155799);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-family: 'Audiowide';
  
}

@keyframes typewriter {
  0% {
    width: 0px;
  }

  100% {
    width: 240px;
  }
}

@keyframes blink {
  0% {
    border-right-color: rgba(255,255,255,.75);
    ;
  }

  100% {
    border-right-color: transparent;
  }
}
.tab-item {
  transition: all 0.3s ease;
}

.tab-item:hover {
  color: #0056b3;
  border-bottom: 3px solid #00f;
  font-weight: bold;
}
body {
  padding-left: 0 !important;
  padding-right: 0 !important;
  margin-left: 0 !important;
  margin-right: 0 !important;
  overflow-x: hidden;
}
.skillscontainer {
  margin: 0 auto;
  padding: 20px;
  width: 100%;
  /* max-width: 1200px; */
  flex: 1;
}

.skill-box {
  background-color: #111;
  border-radius: 15px;
  padding: 20px;
  text-align: center;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

/* Optional: Make icons and texts scale nicely */
.skill-icon {
  max-width: 60px;
  height: auto;
  margin-bottom: 10px;
}

@media (max-width: 768px) {
  .skill-box {
    padding: 15px;
  }

  .skill-icon {
    max-width: 40px;
  }
}
